<template>
  <div class="chart" id="chart_his">

  </div>
</template>
<script setup>
import * as echarts from "echarts";
import {reactive,ref} from 'vue';
import { onMounted, onUnmounted } from "vue";
let echart = echarts;

const option = {
  title: {
    text: '事故统计',
    textStyle:{
      color:'#fff'
    },
    left: 'center',
    top:'20px'
    // subtext: 'Living Expenses in Shenzhen'
  },
  xAxis: {
    type: 'category',
    data: ['火灾事故', '爆炸事故', '伤亡事故', '生产事故', '设备事故', '交通事故', '水污染事故', '大气污染事故', '辐射事故', '振动事故']
  },
  yAxis: {
    type: 'value'
  },
  series: [
    {
      data: [0, 0, 0, 50000, 20000, 0,0],
      type: 'bar'
    }
  ]
};

onMounted(() => {
  initChart();
});

onUnmounted(() => {
  echart.dispose;
});

// 基础配置一下Echarts
function initChart() {
  let chart = echart.init(document.getElementById("chart_his"));
  // 把配置和数据放这里
  chart.setOption(option)

  window.onresize = function () {
    //自适应大小
    chart.resize();
  };
}
</script>
<style lang="less" scoped>
.chart {
  // margin-top: 1vh;
  // margin-left: 2vw;
  height: 46vh;
  width: 66vw;
  background-color: #0b1a41;
  // background: url(../../../assets/images/bgimg.png) no-repeat ;
  background-size: 100% 100%;
}
</style>
