<template>
    <div class="test">
      <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.1rem;line-height: 0.5rem;">产能增长趋势</h2>
      <div class="chart">
        <div id="Trend" :style="{ height: '3.2rem'}"></div>
      </div>
    </div>
  </template>
  
  <script>
  import * as echarts from "echarts";
  import { onMounted, onUnmounted } from "vue";
  export default {
    setup() {
      /// 声明定义一下echart
      let echart = echarts;
  
      onMounted(() => {
        initChart();
      });
  
      onUnmounted(() => {
        // echart.dispose;
        echart.dispose(chart);
      });
  
      // 基础配置一下Echarts
      function initChart() {
        let chart = echart.init(document.getElementById("Trend"));
        // 把配置和数据放这里
        chart.setOption({
          color:["#70DBDB", "#B87333"],
          
          xAxis: {
            
            nameTextStyle: { fontSize: 10 },
            type: "category",
            data: ["2018", "2019","2020", "2021", "2022", "2023"],
            axisLine: {
              lineStyle: {
                color: "#FFF",
                width: 1,
              },
            },
            axisLabel: {
              textStyle: {
                color: "white",
              },
            },
          },
          yAxis: {
            
            nameTextStyle: { fontSize: 10 },
            type: "value",
            axisLine: {
              lineStyle: {
                color: "#FFF",
                width: 1,
              },
              
            },
            axisLabel: {
              textStyle: {
                color: "white",
              },
            },
            splitLine: {
              lineStyle: {
                color: "#2F2F4F",
              },
            },
          },
          series: [
            { name: '改造完成',
              data: [6.9, 7.2, 8.2, 8.9,9.5,9.8],
              type: "line",
              label: {
                show: true,
                color: "white",
                position: "bottom",
              },
            },
          ],
        });
        window.onresize = function () {
          //自适应大小
          chart.resize();
        };
      }
  
      return { initChart };
    },
  };
  </script>
  
  <style scoped>
  .chart {
    /* margin-top: 0.125rem; */
    width: 100%;
    height: 3.2rem; /* 确保这里有明确的高度 */
  }
  </style>

