<!-- A类设备健康状况 -->
<template>
  <div class="test">
    <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.3rem;line-height: 0.5rem;">A类设备健康状况</h2>
    <div class="chart">
      <div id="HChart" :style="{ height: '3rem' }"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { onMounted, onUnmounted } from "vue";

export default {
  setup() {
    /// 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("HChart"));
      // 把配置和数据放这里
      chart.setOption({
        title: {
          text: "健康设备占比: 99.1%           健康设备占比: 99.3%             健康设备占比: 99%              健康设备占比: 100%",
          x:"13%",
          y:"70%",
          textStyle:{
            color: "white",
            fontSize: 12
          }
        },
        legend: {
          top: "2%",
          textStyle: {
            color: "white",
          },
        },
        color: ["#70DBDB", "#B87333"],
        dataset: {
          source: [
            ["location", "健康", "不健康"],
            ["车间A", 540, 5],
            ["车间B", 300, 2],
            ["车间C", 100, 1],
            ["车间D", 50, 0],
          ],
        },
        xAxis: {
          name: "车间",
          type: "category",
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
        },
        yAxis: {
          name: "数量",
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
          splitLine: {
            lineStyle: {
              color: '#2F2F4F',
            },
          },

        },
        grid:{ show:false, top:'20%',bottom:'40%' },

        series: [
          {
            type: "bar",
            barWidth: "25%",

            label: {
              show: true,
              color: "white",
              position: "top",
            },
          },
          {
            type: "bar",
            barWidth: "25%",
            label: {
              show: true,
              color: "white",
              position: "top",
            },
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }

    return { initChart };
  },
};
</script>

<style scoped>
.chart {
  margin-top: 0.125rem;
}
</style>
