<!-- 多晶硅产品质量 -->
<template>
  <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.34rem;line-height: 0.45rem;">多晶硅产品质量</h2>
  <div class="container">
    <section class="left">
      <span
        id="Quality1Chart"
        :style="{ height: '2.7rem', width: '5rem' }"
      ></span>
    </section>
    <section class="right">
      <span
        id="Quality2Chart"
        :style="{ height: '2.7rem', width: '5rem' }"
      ></span>
    </section>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { onMounted, onUnmounted } from "vue";

export default {
  setup() {
    /// 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart1();
      initChart2();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart1() {
      let chart = echart.init(document.getElementById("Quality1Chart"));
      // 把配置和数据放这里
      chart.setOption({
        tooltip: {
          trigger: "item",
        },
        legend: {
          orient: "vertical",
          left: "5%",
          bottom: "20%",
          textStyle: {
            color: "#ffffff", //字体颜色
          },
        },
        color: ["#ADEAEA", "#ED7C30"],
        series: [
          {
            label: {
            normal: {
              formatter: "{c}: ({d}%)",
              textStyle: {
                fontWeight: "normal",
                fontSize: 10,
              },
            },
          },
            name: "Access From",
            type: "pie",
            radius: "50%",
            left: "10%",
            data: [
              { value: 1048, name: "单晶料" },
              { value: 73, name: "多晶料" },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }

    function initChart2() {
      let chart = echart.init(document.getElementById("Quality2Chart"));
      // 把配置和数据放这里
      chart.setOption({
        tooltip: {
          trigger: "item",
        },
        legend: {
          orient: "vertical",
          left: "5%",
          bottom: "20%",
          textStyle: {
            color: "#ffffff", //字体颜色
          },
        },
        color: ["#ADEAEA", "#ED7C30"],
        series: [
          {
            label: {
            normal: {
              formatter: "{c}: ({d}%)",
              textStyle: {
                fontWeight: "normal",
                fontSize: 10,
              },
            },
          },
            name: "Access From",
            type: "pie",
            radius: "50%",
            left: "15%",
            data: [
              { value: 1000, name: "N型单晶料" },
              { value: 100, name: "P型多晶料" },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }

    return { initChart1, initChart2 };
  },
};
</script>

<style scoped>
.container {
  margin-top: 0.125rem;
  display: flex;
}
.container .left {
  flex: 2;
}

.container .right {
  flex: 2;
}
</style>
