<!-- 设备多组度健康分析 -->
<template>
  <div class="test">
    <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.3rem;line-height: 0.5rem;">设备多组度健康分析</h2>
    <div class="chart">
      <div id="MulChart" :style="{ height: '6.3rem' }"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { onMounted, onUnmounted } from "vue";

const weatherIcons = {
  pressure: "~@assets/images/Equipment/Multipul_icon1.jpg",
  temprature: "~@assets/images/Equipment/Multipul_icon2.jpg",
  electronic: "~@assets/images/Equipment/Multipul_icon3.jpg",
  technology: "~@assets/images/Equipment/Multipul_icon4.jpg",
};
const seriesLabel = {
  show: true,
};
export default {
  setup() {
    /// 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("MulChart"));
      // 把配置和数据放这里
      chart.setOption({
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        legend: {
          data: ["健康占比（%）", "不健康占比（%）"],
          top: "5%",
          textStyle:{
            color :'white'
          }
        },
        grid: {
          left: 100,
        },
        xAxis: {
          type: "value",
          //   name: "Days",

          axisLabel: {
            formatter: "{value}",
          },
          splitLine: {
            //网格线
            show: false,
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
        },
        yAxis: {
          type: "category",
          name: "设备本体",
          data: ["pressure", "temprature", "electronic", "technology"],
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
          axisLabel: {
            formatter: function (value) {
              return "{" + value + "| }\n{value|" + value + "}";
            },
            margin: 20,
            rich: {
              value: {
                lineHeight: 30,
                align: "center",
              },
              pressure: {
                height: 40,
                align: "center",
                backgroundColor: {
                  image: weatherIcons.pressure,
                },
              },
              temprature: {
                height: 40,
                align: "center",
                backgroundColor: {
                  image: weatherIcons.temprature,
                },
              },
              electronic: {
                height: 40,
                align: "center",
                backgroundColor: {
                  image: weatherIcons.electronic,
                },
              },
              technology: {
                height: 40,
                align: "center",
                backgroundColor: {
                  image: weatherIcons.technology,
                },
              },
            },
          },
        },
        series: [
          {
            name: "健康占比（%）",
            type: "bar",
            showBackground: true,
            data: [99.1, 98.5, 98.1, 100],
            label: seriesLabel,
            itemStyle: {
              color: "rgba(100, 219, 155, 0.12)",
              borderWidth: 1,
              borderColor: "#70DBDB",
            },
          },
          {
            name: "不健康占比（%）",
            type: "bar",
            showBackground: true,
            label: {
              seriesLabel,
              show: true,
              color: "white",
              position: "right",
            },
            itemStyle: {
              color: "rgba(100, 219, 155, 0.12)",
              borderWidth: 1,
              borderColor: "#B87333",
            },
            data: [0.9, 1.5, 1.9, 0],
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }

    return { initChart };
  },
};
</script>

<style scoped>
.chart {
  margin-top: 0.125rem;
}
</style>
