<!-- 设备数字化改造计划投入 -->
<template>
  <div class="test">
    <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.3rem;line-height: 0.5rem;">设备数字化改造计划投入</h2>
    <div class="chart">
      <div id="UpChart" :style="{ height: '3rem' }"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { onMounted, onUnmounted } from "vue";
export default {
  setup() {
    /// 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("UpChart"));
      // 把配置和数据放这里
      chart.setOption({
        legend: {
          top: "5%",
          fontSize: 10,
          data: ["改造完成", "计划改造"],
          textStyle: {
            color: "white",
            fontSize: 10,
          },
        },
        color:["#70DBDB", "#B87333"],
        xAxis: {
          name: "年份",
          nameTextStyle: { fontSize: 10 },
          type: "category",
          data: ["2020", "2021", "2022", "2023"],
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
          axisLabel: {
            textStyle: {
              color: "white",
            },
          },
        },
        yAxis: {
          name: "数量",
          nameTextStyle: { fontSize: 10 },
          type: "value",
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
            
          },
          axisLabel: {
            textStyle: {
              color: "white",
            },
          },
          splitLine: {
            lineStyle: {
              color: "#2F2F4F",
            },
          },
        },
        series: [
          { name: '改造完成',
            data: [150, 230, 200, 210],
            type: "line",
            label: {
              show: true,
              color: "white",
              position: "bottom",
            },
          },
          { name: '计划改造',
            data: [200, 230, 240, 220],
            type: "line",
            label: {
              show: true,
              color: "white",
              position: "top",
            },
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }

    return { initChart };
  },
};
</script>

<style scoped>
.chart {
  margin-top: 0.125rem;
}
</style>
