<template>
  <div class="chartbox">
    <h2 class="title" style="">硅料入库明细</h2>
    <div class="chart">
      <div id="SiTrend" ref="siTrendChart"></div>
    </div>
  </div>
</template>


<script setup>
import * as echarts from "echarts";
import {reactive,ref} from 'vue';
import { onMounted, onUnmounted } from 'vue';
import axios from '@/axios';


/// 声明定义一下echart
let chart
let siTrendChart = ref(null)
const data1 = ref([])
const x = ['product','Batch A','Batch B','Batch C','Batch D']


onMounted(() => {
  chart = echarts.init(siTrendChart.value);
  console.log(chart);
  
  
  setChart();
  window.onresize = ()=> {
    //自适应大小
    chart.resize();
  };
});

onUnmounted(() => {
  chart.dispose;
});
let final_data1=['免洗\\致密\\块',0,0,0,0]
let final_data2=['免洗\\菜花\\块',0,0,0,0]
let final_data3=['免洗\\珊瑚\\块',0,0,0,0]
let final_data4=['致密\\复投\\N型',0,0,0,0]
console.log(666);


// 基础配置一下Echarts
const setChart = async () => {
  console.log('是否执行setChart')
  
  await axios.get('/overview/siin?param1=2023-07-20 15:00:00&param2=2023-07-21 20:00:00').then(res=>{
    
    if(res){
      data1.value = res
    console.log('>>>>',data1);
    console.log('>>>>11',res);
    console.log(123123);
    data1.value.forEach((e)=>{
      switch(e.batch_category){
        case 'Batch A':
          switch(e.name){
            case '免洗\\致密\\块':
              final_data1[1]=final_data1[1]+e.weight
              break;
            case '免洗\\菜花\\块':
              final_data2[1]=final_data2[1]+e.weight
              break;
            case '免洗\\珊瑚\\块':
              final_data3[1]=final_data3[1]+e.weight
              break;
            case '致密/复投/N型':
              final_data4[1]=final_data4[1]+e.weight
              break;
          }
        break;
        case 'Batch B':
          switch(e.name){
            case '免洗\\致密\\块':
              final_data1[2]=final_data1[2]+e.weight
              break;
            case '免洗\\菜花\\块':
              final_data2[2]=final_data2[2]+e.weight
              break;
            case '免洗\\珊瑚\\块':
              final_data3[2]=final_data3[2]+e.weight
              break;
            case '致密/复投/N型':
              final_data4[2]=final_data4[2]+e.weight
              break;
          }
        break;
        case 'Batch C':
          switch(e.name){
            case '免洗\\致密\\块':
              final_data1[3]=final_data1[3]+e.weight
              break;
            case '免洗\\菜花\\块':
              final_data2[3]=final_data2[3]+e.weight
              break;
            case '免洗\\珊瑚\\块':
              final_data3[3]=final_data3[3]+e.weight
              break;
            case '致密/复投/N型':
              final_data4[3]=final_data4[3]+e.weight
              break;
          }
        break;
        case 'Batch D':
          switch(e.name){
            case '免洗\\致密\\块':
              final_data1[4]=final_data1[4]+e.weight
              break;
            case '免洗\\菜花\\块':
              final_data2[4]=final_data2[4]+e.weight
              break;
            case '免洗\\珊瑚\\块':
              final_data3[4]=final_data3[4]+e.weight
              break;
            case '致密/复投/N型':
              final_data4[4]=final_data4[4]+e.weight
              break;
          }
        break;
      }
    })
    console.log(final_data1,final_data2,final_data3,final_data4);
    }
    return res
  })
  chart.on('updateAxisPointer',(event)=>{
    const xAxisInfo = event.axesInfo[0];
    if (xAxisInfo) {
    const dimension = xAxisInfo.value + 1;
    chart.setOption({
      series: {
        id: 'pie',
        label: {
          // formatter: '{b}: {@[' + dimension + ']} ({d}%)'
        },
        encode: {
          value: dimension,
          tooltip: dimension
        }
      }
    });
  }
  })
  // 把配置和数据放这里
  chart.setOption({
    legend: {
      textStyle: {
      color: "#FFF",
      fontSize:'10px'
    }
    },
    tooltip: {
      trigger: 'axis',
      showContent: false
    },
    dataset: {
      source: [
        x,
        final_data1,
        final_data2,
        final_data3,
        final_data4
      ]
    },
    xAxis: { 
      type: 'category',
      axisLine: {
        lineStyle: {
          color: "#FFF",
          width: 1,
        },
      },
      axisLabel: {
        textStyle: {
          color: "white",
        },
      },
    },
    yAxis: { 
      gridIndex: 0 ,
      axisLine: {
        lineStyle: {
          color: "#FFF",
          width: 1,
        },     
      },
      axisLabel: {
        textStyle: {
          color: "white",
        },
      },
    },
    grid: { top: '55%',
    left:'18% '},
    series: [
      {
        type: 'line',
        smooth: true,
        seriesLayoutBy: 'row',
        emphasis: { focus: 'series' }
      },
      {
        type: 'line',
        smooth: true,
        seriesLayoutBy: 'row',
        emphasis: { focus: 'series' }
      },
      {
        type: 'line',
        smooth: true,
        seriesLayoutBy: 'row',
        emphasis: { focus: 'series' }
      },
      {
        type: 'line',
        smooth: true,
        seriesLayoutBy: 'row',
        emphasis: { focus: 'series' }
      },
      {
        type: 'pie',
        id: 'pie',
        radius: '30%',
        center: ['50%', '25%'],
        emphasis: {
          focus: 'self'
        },
        label: {
          formatter: '{b}: {@Batch A} ({d}%)'
        },
        encode: {
          itemName: 'product',
          value: 'Batch A',
          tooltip: 'Batch A'
        }
      }
    ]
  });
}
      
</script>

<style scoped lang="less">
.chartbox{
  width: 22vw;
  margin-left: 1vw;
  border: 0.15vw solid rgb(5, 38, 85);
  background-color: #0b1234; 
}
.title{
  text-align: center;
  color: rgb(255, 255, 255);
  height:4vh;
  line-height: 4vh;
  background: url('@/assets/images/bgtitle.png') no-repeat;
  background-size: 100% 100%;
}
.chart {
  margin-top: 0.125rem;
  height: 47vh;
 

  #SiTrend{
    margin-top: 0.125vh;
    height: 47vh;
  }
}
</style>
