<template>
  <div class="ratio">
    <div class="title" style="text-align: center;padding-top:0.5em;">人员安全相关比例</div>
    <div class="chart" id="chart_gas">
    </div>
  </div>
</template>

<script setup>
import * as echarts from "echarts";
import {reactive,ref} from 'vue';
import { onMounted, onUnmounted } from "vue";
import { size } from "lodash";
let echart = echarts;
const data = [
  {
    name: '质检分析岗',
    value: 10
  },
  {
    name: '电气管理人员',
    value: 10
  },
  {
    name: '电气运行',
    value: 10
  },
  {
    name: '电气维护',
    value: 10
  },
  {
    name: '电气还原',
    value: 10
  },
];
const data1 = [
  {
    name: '噪声',
    value: 10
  },
  {
    name: '工频电场辐射',
    value: 10
  },
  {
    name: '粉尘',
    value: 10
  },
  {
    name: '酸雾',
    value: 10
  }
];
const data2 = [
  {
    name: '接触物理因素类',
    value: 1
  },
  {
    name: '接触粉尘类',
    value: 1
  },
  {
    name: '接触化学因素类',
    value: 1
  },
  {
    name: '接触放射因素类',
    value: 1
  },
];
onMounted(() => {
  initChart();
});

onUnmounted(() => {
  echart.dispose;
});

// 基础配置一下Echarts
function initChart() {
  let chart = echart.init(document.getElementById("chart_gas"));
  // 把配置和数据放这里
  chart.setOption({
  title: [
    {
      subtext: '危害因素比例',
      left: '16.67%',
      top: '85%',
      textAlign: 'center',
      subtextStyle: {
        color: '#fff',
        fontSize: 18,
        fontWeight: 600
      }
    },
    {
      subtext: '人员岗位比例',
      left: '50%',
      top: '85%',
      textAlign: 'center',
      subtextStyle: {
        color: '#fff',
        fontSize: 18,
        fontWeight: 600
      }
    },
    {
      subtext: '接触类型比例',
      left: '83.33%',
      top: '85%',
      textAlign: 'center',
      subtextStyle: {
        color: '#fff',
        fontSize: 18,
        fontWeight: 600
      }
    }
  ],
  series: [
    {
      type: 'pie',
      radius: '50%',
      center: ['50%', '50%'],
      data: data1,
      label: {
        position: 'outer',
        alignTo: 'none',
        bleedMargin: 5,
        color:'#fff'
      },
      left: 0,
      right: '66.6667%',
      top: 0,
      bottom: 0
    },
    {
      type: 'pie',
      radius: '50%',
      center: ['50%', '50%'],
      data: data,
      label: {
        position: 'outer',
        alignTo: 'labelLine',
        bleedMargin: 5,
        color:'#fff'
      },
      left: '33.3333%',
      right: '33.3333%',
      top: 0,
      bottom: 0
    },
    {
      type: 'pie',
      radius: '50%',
      center: ['50%', '50%'],
      data: data2,
      label: {
        position: 'outer',
        alignTo: 'edge',
        margin: 20,
        color:'#fff'
      },
      left: '66.6667%',
      right: 0,
      top: 0,
      bottom: 0
    }
  ]
});
  window.onresize = function () {
    //自适应大小
    chart.resize();
  };
}
</script>

<style lang="less" scoped>
.ratio {
  margin-top: 2vh;
  // margin-left: 1vw;
  // margin-top: 1vh;
  border: 0.2vw solid rgb(5, 38, 85);
  background-color: #fff;
}
.title{
  // width: 59.625vw;
  height: 5vh;
  margin-left: 0vw;
  background: url('@/assets/images/bgtitle.png') no-repeat;
  background-size: 100% 100%;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
}
.chart {
  margin-top: 0vh;
  // padding-top: 1vh;
  height: 26.625vh;
  width: 59.625vw;
  background: url('@/assets/images/bgimg.png') no-repeat;
  background-size: 100% 100%;
}
</style>