<template>
  <div class="chart" id="chart_his">
  </div>
</template>
<script setup>
import * as echarts from "echarts";
import {reactive,ref} from 'vue';
import { onMounted, onUnmounted } from "vue";
import axios from '@/axios';
let echart = echarts;
let chart
// let base = +new Date(2021, 5, 3);
// let oneDay = 24 * 3600 * 1000;
// let date = [];
// let data = [Math.random() * 300];
// for (let i = 1; i < 2000; i++) {
//   var now = new Date((base += oneDay));
//   date.push([now.getFullYear(), now.getMonth() + 1, now.getDate()].join('/'));
//   data.push(Math.round((Math.random() - 0.5) * 20 + data[i - 1]));
// }
const date = ref([])
const data = ref([])



onMounted(() => {
  chart = echart.init(document.getElementById("chart_his"));
  setChart();
  // console.log(date,data);
  window.onresize = ()=> {
    //自适应大小
    chart.resize();
  };
});

onUnmounted(() => {
  echart.dispose;
});

// 基础配置一下Echarts
const  setChart = async()=> {
  await axios.get('/historychart').then(res=>{
    date.value= res.hours
    data.value = res.count
    return res
  })

  let option = {
    tooltip: {
      trigger: 'axis',
      position: function (pt) {
        return [pt[0], '10%'];
      }
    },
    title: {
      left: 'center',
      text: '历史报警统计',
      textStyle:{
        color: '#fff'
      }
    },
    toolbox: {
      feature: {
        dataZoom: {
          yAxisIndex: 'none'
        },
        restore: {},
        saveAsImage: {}
      }
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      data: date.value
    },
    yAxis: {
      type: 'value',
      boundaryGap: [0, '100%']
    },
    dataZoom: [
      {
        type: 'inside',
        start: 0,
        end: 10
      },
      {
        start: 0,
        end: 10
      }
    ],
    series: [
      {
        name: '报警数量',
        type: 'line',
        symbol: 'none',
        sampling: 'lttb',
        itemStyle: {
          color: 'rgb(255, 70, 131)'
        },
        areaStyle: {
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: 'rgb(255, 158, 68)'
            },
            {
              offset: 1,
              color: 'rgb(255, 70, 131)'
            }
          ])
        },
        data: data.value
      }
    ]
  };

  // 把配置和数据放这里
  chart.setOption(option)

}
</script>
<style lang="less" scoped>
.chart {
  margin-top: 1vh;
  margin-left: 2vw;
  height: 40vh;
  width: 23vw;
  background-color: #0b1a41;
  // background: url(../../../assets/images/bgimg.png) no-repeat ;
  background-size: 100% 100%;
}
</style>
