<!-- 计划执行 -->
<template>
  <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.34rem;line-height: 0.45rem;">计划执行</h2>
  <div class="chart">
    <div id="PlanChart" :style="{ height: '3rem' }"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { onMounted, onUnmounted } from "vue";

export default {
  setup() {
    /// 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("PlanChart"));
      // 把配置和数据放这里
      chart.setOption({
        legend: {
          top: "5%",
          textStyle: {
            color: "white",
          },
        },
        color:["#ADEAEA", "#ED7C30"],
        dataset: {
          source: [
            ["product", "产量", "计划"],
            ["3月1日", 0.27, 0.27],
            ["3月2日", 0.37, 0.37],
            ["3月3日", 0.34, 0.34],
            ["3月4日", 0.23, 0.23],
            ["3月5日", 0.29, 0.29],
            ["3月6日", 0.43, 0.43],
            ["3月7日", 0.45, 0.45],
          ],
        },
        xAxis: {
          name: "日期",
          type: "category",
          axisLabel: {
            textStyle: {
              color: "white",
            },
          },
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
        },
        yAxis: {
          name: "产量(万吨)",
          min: 0,
          max: 0.5,
          interval: 0.1,
          splitLine: {
            lineStyle: {
              color: '#2F2F4F',
            },
          },
          axisLabel: {
            textStyle: {
              color: "white",
            },
          },
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
        },
        series: [
          {
            type: "bar",
            label: {
              show: true,
              color: "white",
              position: "top",
            },
          },
          {
            type: "bar",
            label: {
              show: true,
              color: "white",
              position: "top",
            },
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }

    return { initChart };
  },
};
</script>

<style scoped>
.chart {
  margin-top: 0.125rem;
}
</style>
