  <template>
    <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.3rem;line-height: 0.5rem;">产品质量</h2>
    <div class="chart">
      <div id="Quality" :style="{ height: '3rem' }"></div>
    </div>
  </template>
  
  <script>
  import * as echarts from "echarts";
  import { onMounted, onUnmounted } from "vue";
  
  export default {
    setup() {
      /// 声明定义一下echart
      let echart = echarts;
  
      onMounted(() => {
        initChart();
      });
  
      onUnmounted(() => {
        echart.dispose;
      });
  
      // 基础配置一下Echarts
      function initChart() {
        let chart = echart.init(document.getElementById("Quality"));
        // 把配置和数据放这里
        chart.setOption({
          legend: {
            top: "5%",
            textStyle: {
              color: "white",
            },
          },
          color:["#ADEAEA", "#ED7C30","#3D7530"],
          dataset: {
            source: [
              ["product", "致密料比例", "菜花料比例","珊瑚料比例"],
              ["116A", 57.69,16.32,25.99],
              ["116B", 44.22,16.76,39.02],
              ["116C", 53.74,14.12,32.13],
              ["116D", 47.80,22.02,20.08],
            ],
          },
          xAxis: {
            type: "category",
            axisLabel: {
              textStyle: {
                color: "white",
              },
            },
            axisLine: {
              lineStyle: {
                color: "#FFF",
                width: 1,
              },
            },
          },
          yAxis: {
            min: 0,
            max: 50,
            interval: 10,
            splitLine: {
              lineStyle: {
                color: '#2F2F4F',
              },
            },
            axisLabel: {
              textStyle: {
                color: "white",
              },
            },
            axisLine: {
              lineStyle: {
                color: "#FFF",
                width: 1,
              },
            },
          },
          series: [
          {
            type: "bar",
            color: {
              type: "linear",
              x: 0, //右
              y: 0, //下
              x2: 0, //左
              y2: 1, //上
              colorStops: [
                {
                  offset: 0,
                  color: "#2F4F4F", // 0% 处的颜色
                },
                {
                  offset: 1,
                  color: "#8FBC8F", // 100% 处的颜色
                },
              ],
            },
          },
          {
            type: "bar",
    
            color: {
              type: "linear",
              x: 0, //右
              y: 0, //下
              x2: 0, //左
              y2: 1, //上
              colorStops: [
                {
                  offset: 0,
                  color: "#11d56d", // 0% 处的颜色
                },
                {
                  offset: 1,
                  color: "#83bff6", // 100% 处的颜色
                },
              ],
            },
          },
          {
            type: "bar",
           
            color: {
              type: "linear",
              x: 0, //右
              y: 0, //下
              x2: 0, //左
              y2: 1, //上
              colorStops: [
                {
                  offset: 0,
                  color: "#CD7F32", // 0% 处的颜色
                },
                {
                  offset: 1,
                  color: "#D8D8BF", // 100% 处的颜色
                },
              ],
            },
          },
          
          ],
        });
        window.onresize = function () {
          //自适应大小
          chart.resize();
        };
      }
  
      return { initChart };
    },
  };
  </script>
  
  <style scoped>
  .chart {
    margin-top: 0.125rem;
  }
  </style>
  