<!-- 智能设备投入（万元） -->
<template>
  <div class="test">
    <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.3rem;line-height: 0.5rem;">智能设备投入（万元）</h2>
    <div class="chart">
      <div id="CostChart" :style="{ height: '3rem' }"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { onMounted, onUnmounted } from "vue";
export default {
  setup() {
    /// 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("CostChart"));
      // 把配置和数据放这里
      chart.setOption({
        legend: {
          top: "1%",
          fontSize: 10,
          textStyle: {
            color: "white",
            fontSize: 10,
          },
        },
        grid: {},
        dataset: {
          source: [
            ["location", "A类设备", "B类设备", "C类设备", "D类设备"],
            ["2021", 750, 500, 155, 52],
            ["2022", 600, 470, 182, 51],
            ["2023", 400, 300, 139, 45],
          ],
        },
        xAxis: {
          type: "category",
          name: "年份",
          nameTextStyle: { fontSize: 10 },
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
            fontSize: 5,
          },
          axisLabel: {
            textStyle: {
              color: "white",
            },
          },
        },
        yAxis: {
          name: "金额",
          nameTextStyle: { fontSize: 10 },
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
          axisLabel: {
            textStyle: {
              color: "white",
            },
          },
          splitLine: {
            lineStyle: {
              color: "#2F2F4F",
            },
          },
        },

        series: [
          {
            type: "bar",
            label: {
              show: true,
              position: "top",
              color: "white",
            },
            color: {
              type: "linear",
              x: 0, //右
              y: 0, //下
              x2: 0, //左
              y2: 1, //上
              colorStops: [
                {
                  offset: 0,
                  color: "#11d56d", // 0% 处的颜色
                },
                {
                  offset: 1,
                  color: "#83bff6", // 100% 处的颜色
                },
              ],
            },
          },
          {
            type: "bar",
            label: {
              show: true,
              position: "top",
              color: "white",
            },
            color: {
              type: "linear",
              x: 0, //右
              y: 0, //下
              x2: 0, //左
              y2: 1, //上
              colorStops: [
                {
                  offset: 0,
                  color: "#CD7F32", // 0% 处的颜色
                },
                {
                  offset: 1,
                  color: "#D8D8BF", // 100% 处的颜色
                },
              ],
            },
          },
          {
            type: "bar",
            label: {
              show: true,
              position: "top",
              color: "white",
            },
            color: {
              type: "linear",
              x: 0, //右
              y: 0, //下
              x2: 0, //左
              y2: 1, //上
              colorStops: [
                {
                  offset: 0,
                  color: "#2F4F4F", // 0% 处的颜色
                },
                {
                  offset: 1,
                  color: "#8FBC8F", // 100% 处的颜色
                },
              ],
            },
          },
          {
            type: "bar",
            label: {
              show: true,
              position: "top",
              color: "white",
            },
            color: {
              type: "linear",
              x: 0, //右
              y: 0, //下
              x2: 0, //左
              y2: 1, //上
              colorStops: [
                {
                  offset: 0,
                  color: "#FF6E47", // 0% 处的颜色
                },
                {
                  offset: 1,
                  color: "#EAADEA", // 100% 处的颜色
                },
              ],
            },
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }

    return { initChart };
  },
};
</script>

<style scoped>
.chart {
  margin-top: 0.125rem;
}
</style>
