<!-- 质量-Cpk参数 -->
<template>
  <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.34rem;line-height: 0.45rem;">质量-Cpk参数</h2>
  <div class="chart">
    <div id="TestChart" :style="{ height: '2.4rem' }"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { onMounted, onUnmounted } from "vue";

export default {
  setup() {
    /// 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("TestChart"));
      // 把配置和数据放这里
      chart.setOption({
        tooltip: {
          trigger: "axis",
        },
        legend: {
          top:"5%",
          data: ["A顶温", "B顶温", "C顶温"],
          textStyle: {
            color: "white",
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          data: ["03-01", "03-02", "03-03", "03-04", "03-05"],
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
        },
        yAxis: {
          min:95, 
          max:100, 
          interval:1,
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1,
            },
          },
        },
        series: [
          {
            name: "A顶温",
            type: "line",
            smooth: true,
            data: [99, 96, 97, 98, 99],
          },
          {
            name: "B顶温",
            type: "line",
            smooth: true,
            data: [96, 98, 97, 96, 98],
          },
          {
            name: "C顶温",
            type: "line",
            smooth: true,
            data: [97, 96, 98, 96, 98],
          }
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }

    return { initChart };
  },
};
</script>

<style scoped>
.chart {
  margin-top: 0.125rem;
}
</style>
