<template>
  <div class="recall">
    <div class="title" style="text-align: center;padding-top:0.5em;">各环节废物排放比例</div>
    <div class="chart" id="chart_gas">
    </div>
  </div>
</template>

<script setup>
import * as echarts from "echarts";
import {reactive,ref} from 'vue';
import { onMounted, onUnmounted } from "vue";
import { size } from "lodash";
let echart = echarts;
const data = [
  {
    name: '系统置换尾气',
    value: 10
  },
  {
    name: '腐蚀酸雾',
    value: 10
  },
  {
    name: '硅粉上料',
    value: 10
  },
  {
    name: '多晶硅破碎',
    value: 10
  },
  {
    name: '无组织排放',
    value: 10
  },
];
const data1 = [
  {
    name: '循环冷却水排污水',
    value: 10
  },
  {
    name: '酸性废水',
    value: 10
  },
  {
    name: '含氟废水',
    value: 10
  },
  {
    name: '生活污水',
    value: 10
  }
];
const data2 = [
  {
    name: '残渣处理渣',
    value: 1
  },
  {
    name: '废水处理站',
    value: 1
  },
  {
    name: '制氮系统',
    value: 1
  },
  {
    name: '氢化系统',
    value: 1
  },
  {
    name: '反歧化系统',
    value: 1
  },
  {
    name: '纯水、软水制备系统',
    value: 1
  },
  {
    name: '干法回收系统',
    value: 1
  },
  {
    name: '压缩机、冷冻机设备检修',
    value: 1
  }
];
onMounted(() => {
  initChart();
});

onUnmounted(() => {
  echart.dispose;
});

// 基础配置一下Echarts
function initChart() {
  let chart = echart.init(document.getElementById("chart_gas"));
  // 把配置和数据放这里
  chart.setOption({
  title: [
    {
      subtext: '废水',
      left: '16.67%',
      top: '85%',
      textAlign: 'center',
      subtextStyle: {
        color: '#fff',
        fontSize: 18,
        fontWeight: 600
      }
    },
    {
      subtext: '废气',
      left: '50%',
      top: '85%',
      textAlign: 'center',
      subtextStyle: {
        color: '#fff',
        fontSize: 18,
        fontWeight: 600
      }
    },
    {
      subtext: '废渣',
      left: '83.33%',
      top: '85%',
      textAlign: 'center',
      subtextStyle: {
        color: '#fff',
        fontSize: 18,
        fontWeight: 600
      }
    }
  ],
  series: [
    {
      type: 'pie',
      radius: '50%',
      center: ['50%', '50%'],
      data: data1,
      label: {
        position: 'outer',
        alignTo: 'none',
        bleedMargin: 5,
        color:'#fff'
      },
      left: 0,
      right: '66.6667%',
      top: 0,
      bottom: 0
    },
    {
      type: 'pie',
      radius: '50%',
      center: ['50%', '50%'],
      data: data,
