        alignTo: 'labelLine',
        bleedMargin: 5,
        color:'#fff'
      },
      left: '33.3333%',
      right: '33.3333%',
      top: 0,
      bottom: 0
    },
    {
      type: 'pie',
      radius: '50%',
      center: ['50%', '50%'],
      data: data2,
      label: {
        position: 'outer',
        alignTo: 'edge',
        margin: 20,
        color:'#fff'
      },
      left: '66.6667%',
      right: 0,
      top: 0,
      bottom: 0
    }
  ]
});
  window.onresize = function () {
    //自适应大小
    chart.resize();
  };
}
