<template>
  <div>
    <div class="chart" id="chart"></div>
  </div>
</template>
<script setup>
import * as echarts from "echarts";
import {reactive,ref} from 'vue';
import { onMounted, onUnmounted } from "vue";
let echart = echarts;
const option = reactive({
  title:{
    left: "center",
    top:'0%',
    text:'TCS阀门开度',
    textStyle: {
      fontSize: 15,
      color:'#fff'
    },
  },
  series: [
    {
      
      type: 'gauge',
      progress: {
        show: true,
        width: 18
      },
      axisLine: {
        lineStyle: {
          width: 18
        }
      },
      axisTick: {
        show: false
      },
      splitLine: {
        length: 15,
        lineStyle: {
          width: 2,
          color: '#999'
        }
      },
      axisLabel: {
        distance: 25,
        color: '#999',
        fontSize: 0
      },
      anchor: {
        show: true,
        showAbove: true,
        size: 25,
        itemStyle: {
          borderWidth: 10
        }
      },
      detail: {
        valueAnimation: true,
        fontSize: 20,
        color: '#fff',
        // offsetCenter: [0, '70%'],
        formatter: "{value}%"
      },
      data: [
        {
          value: '20',
          // name:"Percent"
        }
      ]
    }
  ]
})
onMounted(() => {
  initChart();
});

onUnmounted(() => {
  echart.dispose;
});

// 基础配置一下Echarts
function initChart() {
  let chart = echart.init(document.getElementById("chart"));
  // 把配置和数据放这里
  chart.setOption(option)

  window.onresize = function () {
    //自适应大小
    chart.resize();
  };
}
</script>
<style lang="less" scoped>
.chart {
  margin-top: 0vh;
  padding-top: 1vh;
  height: 20vh;
  width: 12.5vw;
}
</style>
