<template>
    <h2 style="text-align: center; color: rgb(255, 255, 255);height:0.3rem;line-height: 0.5rem;">计划执行情况</h2>
    <div class="chart">
      <div id="Plan" :style="{ height: '3rem' }"></div>
    </div>
  </template>
  
  <script>
  import * as echarts from "echarts";
  import { onMounted, onUnmounted } from "vue";
  
  export default {
    setup() {
      /// 声明定义一下echart
      let echart = echarts;
  
      onMounted(() => {
        initChart();
      });
  
      onUnmounted(() => {
        echart.dispose;
      });
  
      // 基础配置一下Echarts
      function initChart() {
        let chart = echart.init(document.getElementById("Plan"));
        // 把配置和数据放这里
        chart.setOption({
          legend: {
            top: "5%",
            textStyle: {
              color: "white",
            },
          },
          grid: {
          left: "3%",
          right: "3%",
          bottom: "20%",
          containLabel: true,
        },
          color:["#ADEAEA", "#ED7C30"],
          dataset: {
            source: [
              ["product", "产量", "计划"],

              ["2019", 330, 300],
              ["2020", 350, 340],
              ["2021", 370, 350],
              ["2022", 380, 380],
              ["2023", 390, 380],
            ],
          },
          xAxis: {
            type: "category",
            axisLabel: {
              textStyle: {
                color: "white",
              },
            },
            axisLine: {
              lineStyle: {
                color: "#FFF",
                width: 1,
              },
            },
          },
          yAxis: {
            name: "产量(万吨)",
            min: 0,
            max: 400,
            interval: 100,
            splitLine: {
              lineStyle: {
                color: '#2F2F4F',
              },
            },
            axisLabel: {
              textStyle: {
                color: "white",
              },
            },
            axisLine: {
              lineStyle: {
                color: "#FFF",
                width: 1,
              },
            },
          },
          series: [
            {
              type: "bar",
              label: {
                show: true,
                color: "white",
                position: "top",
              },
            },
            {
              type: "bar",
              label: {
                show: true,
                color: "white",
                position: "top",
              },
            },
          ],
        });
        window.onresize = function () {
          //自适应大小
          chart.resize();
        };
      }
  
      return { initChart };
    },
  };
  </script>
  
  <style scoped>
  .chart {
    margin-top: 0.125rem;
  }
  </style>
  